export default {
  name: "header.usercenter",
  components: {},
  data() {
    return {}
  },
  methods: {
    loginout: function () {
      this.$store.commit('loginout');
    },
    on_user_center_click: function () {
      if (!this.$store.state.hasLogin) {
        this.$app.show_login_dialog();
      }
    },
  }
}
